.btn-tooltip {
    border: none;
    padding: 0;
    margin: 0;
    cursor: pointer;
    border-radius: 50%;
    color: #5C5C5C;
    background: unset;
    font-size: 1.5rem;
}


/*  tooltip  active  */
[data-bs-toggle="tooltip"]:active {
    .tooltip {
        display: none !important;
    }
}

/*  tooltip  */
[data-bs-toggle="tooltip"]:focus {
    outline: none;
}

/*  tooltip  */
[data-bs-toggle="tooltip"]:not(:hover):not(:focus-visible) {
    .tooltip {
        opacity: 0;
        visibility: hidden;
    }
}

.tooltip .tooltip-inner {
    text-align: left !important;
}